import { FC } from "react";
import { StatisticsComponentProps } from "../ObjectGUIComponentFactory";
import { ContGraph } from "../Utilities/UtilComponents/ContGraph";
import { IContBlockStatistics } from "../../Adapters/ContBlocksAdapter";


/**
 * React komponenta pro zobrazen statistik Integrtoru
 * @param props 
 * @returns React komponenta editanho okna
 */
export const IntegratorStatistics : FC<StatisticsComponentProps> = (props) => {
        
    const values = (props.statistics as IContBlockStatistics).integratorRecords[props.id];

    return (
        <div>
            <p>Graf hodnoty stavov promnn v ase pro integrtor : {props.id}</p>
            <div>
                <ContGraph data={values} xKey={"time"} yKey={"value"} xLabel="Modelov as" yLabel="Hodnota stavov promnn" />
            </div>
        </div>
    )
  }
