import { IPetriNetsStatistics } from "Editor/Adapters";
import { FC } from "react";
import { StatisticsComponentProps } from "../ObjectGUIComponentFactory";
import { DiscreteGraph } from "../Utilities/UtilComponents/DiscreteGraph";



/**
 * React komponenta pro zobrazen statistik pechodu zskanch v prbhu simulace.
 * @param props Struktura statistik obsahujc kolekci hodnot a kle pro osy grafu 
 * @returns React komponenta okna statistik
 */
export  const TransitionStatistics : FC<StatisticsComponentProps> = (props) => {

    const values = (props.statistics as IPetriNetsStatistics).transitionRecords[props.id];

    return (
        <div>
            <p>Graf kolikrt byl pechod proveden v ase: {props.id}</p>
            <div >
                <DiscreteGraph  data={values} xKey={"time"} yKey={"fired"} xLabel="Modelov as" yLabel="Poet proveden"  />
            </div>
        </div>
    )
  }

