import { Direction } from "Editor/Components/Utilities/UtilMethodsAndTypes";
import { Coordinates, ICoordinates, isCoordinates, IToSerializable } from "./Coordinates";

/**
 * Rozhran bodu
 */
export interface IPoint {
   /**
    * Identifiktor
    */
    id : string
    /**
     * Hodnota souadnic bodu
     */
    coords : ICoordinates
}

/**
 * Potadlo pro tvorbu id bodu
 */
let POINT_COUNTER = 0;

/**
 * Nastav potadlo vytvoench identifiktoru
 * @param value 
 */
export function SetPointCounter(value : number) {
    POINT_COUNTER = value;
}

/**
 * Bod v halvn ploe
 */
export class Point implements IPoint, IToSerializable<IPoint> {
    /**
     * Identifiktor
     */
    public id : string
    
    /**
     *  Absolutn souadnice  bodu v ploe
     * */  
    public coords : Coordinates 

    /**
     * Potadlo pro tvorbu identifiktor
     */
    private static _cnt : number = 0;

    public static get cnt() {
        return POINT_COUNTER++;
    }

    /**
     * Genertor uniktnch id pro konstrukci Point
     * @returns uniktn id
     */
    public static getId() {
        return `Point_${Point.cnt}`
    }
    
    constructor (value : IPoint | ICoordinates) {
        if (isCoordinates(value)) {
            this.id = Point.getId();
            this.coords = new Coordinates(value);
        } else {
            this.id = value.id;
            this.coords = new Coordinates(value.coords);   
        }
    }


    public toSerializableObj () : IPoint {
        return {id : this.id, coords: this.coords.toSerializableObj()};
    }
}


/**
 * Pomocn typ pro parametry konstruktoru EndPointu
 */
export type IEndPointBrief = {
    //  Souadnice
    coords : ICoordinates
    // Typ
    type : EndPointType
    // Omezen potu vstupnch hran
    maxSpawnedObj?: number
    // Smr kterm se m hrana z EndPointu pidat
    arrowDirection? : Direction
    // Jesti je mon s bode spojovat
    connectable?: boolean
} 

/**
 * Rozrahn EndPointu
 */
export interface IEndPoint extends IPoint {
    ownerId : string
    bindings : string[]
    type : EndPointType
    spawnedObjCnt : number
    maxSpawnedObj?: number
    arrowDirection? : Direction,
    connectable: boolean // jestli do nj me vst hrana
}

/**
 * Typ EndPointu   
 */
export enum EndPointType {
    /**
     * Neme z nj vst hrana
     */
    Input,  
    /**
     * Me z nj vst libovoln poet hran
     */
    Infinite,

    /**
     * Me z nj vst poet hran uren parametrem
     */
    Restricted,  
}


/**
 *  Tda koncovho bodu.
 *  Tento bod slou pro spojovn jednotlivch objekt hlavn plochy.
 * 
 */
export class EndPoint  extends Point implements IToSerializable<IEndPoint> {
    /**
     * Id objketu ktermu bod nle 
     */
    public ownerId : string

    /**
     * Bindingy, slou pro nvzn bodu na jin bod.
     */
    public bindings : string[] = []

    /**
     * Typ endpointu
     */
    public type : EndPointType 
    public spawnedObjCnt : number = 0

    /**
     * Definovan omezen pro poet hran vedoucch z endpointu. Plat jen pro urit typ endPointu.
     */
    public maxSpawnedObj? : number  // 0 znamena inputOnly

    /**
     * Smr ipky, jen udv jakm smrem se pid nov hrana
     */
    public arrowDirection? : Direction

    /**
     * Jestli mohou vst hrany do endpointu
     */
    public connectable : boolean

    constructor(value : ICoordinates, ownerId : string, type : EndPointType, maxSpawnedObj? : number, arrowDirection? : Direction, connectable: boolean = true ) {
        super(value);
        this.ownerId = ownerId;
        this.type = type;
        this.arrowDirection = arrowDirection;
        this.connectable = connectable;

        if (maxSpawnedObj) {
            if (maxSpawnedObj >= 1)
                this.maxSpawnedObj = maxSpawnedObj;
            else 
                console.warn("value of maxSpawnedObj cannot be less than one")
        }
    
    }

    public toSerializableObj () : IEndPoint {
        return {
            ...super.toSerializableObj(),
            ownerId: this.ownerId,
            spawnedObjCnt: this.spawnedObjCnt,
            bindings : this.bindings,
            type : this.type,
            arrowDirection: this.arrowDirection,
            maxSpawnedObj: this.maxSpawnedObj,
            connectable: this.connectable
        }
    }
}