export interface IToSerializable<T> {
    /**
    * Metoda vrac serializovatelnou kopii objektu
    * @returns Serializovateln objekt
    */
    toSerializableObj () : T;
}

export interface ICoordinates {
    x : number
    y: number
}

/**
 * Provede typovou kontrolu jestli typ vstupnho objektu odpovd td Coordinates.
 * @param obj vstupn objektu
 * @returns true okud typ odpovd
 */
export function isCoordinates(obj : any) : obj is ICoordinates {
    return (obj as ICoordinates).x !== undefined && (obj as ICoordinates).y !== undefined;
} 

/**
 * Objekty tto tdy slou k uchovn a prci se souadnicemi. 
 */
export class Coordinates implements ICoordinates, IToSerializable<ICoordinates> {

    public constructor(obj : ICoordinates);
    public constructor(obj?: ICoordinates) {
        this.x = obj?.x ?? 0;
        this.y = obj?.y ?? 0;
    }

    /**
     * Souadnice na ose x
     */
    public x : number = 0;
    /**
     * Souadnice na ose y
     */
    public y : number = 0;

    /**
     * Pite souadnice pijatho objektu tm v instanci.
     * @param obj souadnice kter maj bt piteny
     * @returns Odkaz na instanci objektu
     */
    public add (obj : ICoordinates) {
        this.x += obj.x;
        this.y += obj.y;
        return this;
    }

    /**
     * Provede odeet souadnic pijatho objektu od tch v instanci.
     * @param obj Souadnice kter slou jako druh operand operace
     * @returns Odkaz na instanci objektu
     */
    public sub (obj : ICoordinates) {
        this.x -= obj.x;
        this.y -= obj.y;
        return this;
    }

    /**
     * Vynsob souadnice v instanci s tmi v pijatm objektu.
     * @param obj Souadnice kter slou jako druh operand 
     * @returns Odkaz na instanci objektu
     */
    public mul (obj : ICoordinates) {
        this.x *= obj.x;
        this.y *= obj.y;
        return this;
    }

    /**
     * Vydl souadnice instance.
     * @param obj Souadnice kter slou jako druh operand 
     * @returns Odkaz na objekt
     */
    public div (obj : ICoordinates) {
        this.x /= obj.x;
        this.y /= obj.y;
        return this;
    }

    /**
     * Zvt souadnice podle pijat hodnoty.
     * @param obj Hodnota zvten 
     * @returns Odkaz na objekt
     */
    public scale (value : number) {
        this.mul({x: value, y: value});
        return this;
    }

    /**
     * Provede umocnn souadnic podle pijat hodnoty
     * @param value Na koliktou se maj souadnce umocnit
     * @return Odaz na instanci objektu
     */
    public power (value : number) {
        this.x =  Math.pow(this.x, value);
        this.y = Math.pow(this.y, value);
        return this;
    }

    /**
     * Statick metoda pro vpoet vzdlenosti mezi dvma body.
     * @param begin Prvn bod 
     * @param end Druh bod
     * @returns Vzdlenost mezi obma body
     */
    public static getDistance (begin : ICoordinates, end : ICoordinates) {
        const result = new Coordinates(end) // B
        result.sub(begin);  // B - A      
        result.power(2);    // (B - A)^2
        return Math.sqrt(result.x + result.y);  // sqrt((A-B)^2)
    }
    
    toSerializableObj(): ICoordinates {
        return {x: this.x, y: this.y}
    }

    public toString () {
        return `${this.x} ${this.y}`
    }
}