

/**
 * Rozhran definujc strukturu objektu parametr simulace PN
 */
export interface IPNSimulationParams {
    /**
     * Koncov as
     */
    endTime : number
}

/**
 * Vchoz parametry simulace PN
 */
export const defaultPNSimulationParams : IPNSimulationParams = {
    endTime: 10
}

/**
 * Rozhran definujc strukturu objektu s parametry simulace Spojitho blokovho schma
 */
export interface IContBlocksSimulationParams {
    /**
     * Poten modelov as
     */
    beginTime : number
    /**
     * Koncov modelov as
     */
    endTime : number
    /**
     * Dlka kroku numerick metody 
     */
    simStepSize : number
    /**
     * Interval sbru statistik
     */
    statisticsInterval : number
}

/**
 * TypeGuard => provede typovou kontrolu jestli se jedn o objekt odpovdajc rozhran IContBlockSimulationParams
 * @param obj  vstupn objekt
 * @returns true pokud je objekt poadovanho typu
 */
export function isIContBlocksSimulationParams(obj : any) : obj is IContBlocksSimulationParams {
    return obj.endTime != null && obj.simStepSize != null && obj.statisticsStepSize != null
}

/**
 * Vchoz hodnota parametr Spojitho blokovho schma
 */
export const defaultContBlocksSimulationParams : IContBlocksSimulationParams = {
    beginTime: 0,
    endTime: 30,
    simStepSize: 0.001,
    statisticsInterval: 100
}